import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
export default {
  namespaced: true,
  state: {
    leftbar_show: false,
    documentwidth: 0,
    leftbar: {
      nowidx: 1
    },
    rili: {
      data: {},
    },
    userinfo: {
      userlist: {}
    },
    editor: {
      showflg: false,
      time1: '',
      time2: ''
    },
    msg: {
      flg: false,
      content: '',
      q: ''
    },
    missioninfo: {},
    progressinfo: {},
    userinfo: {},
    userlist: {},
    datepicker: false,
    setwhichtime: '',
    chart: {
      getpostnum_c: {},
      gethuoyue: {}
    },
    msgnum: 0
  },
  actions: {
    getmsgnum({
      commit
    }) {
      Vue.prototype.$http('/getmsgnum', {
        uid: localStorage.getItem('uid'),
      }, rs => {
        commit('_getmsgnum', rs)
      })
    },
    gethuoyue({
      commit
    }) {
      Vue.prototype.$http('/gethuoyue', {
        uid: localStorage.getItem('uid')
      }, rs => {
        commit('_gethuoyue', rs)
      })
    },
    getpostnum_c({
      commit
    }) {
      Vue.prototype.$http('/getpostnum_c', {
        uid: localStorage.getItem('uid')
      }, rs => {
        commit('_getpostnum_c', rs)
      })
    },
    closedatepicker({
      commit
    }) {
      commit('_closedatepicker')
    },
    opendatepicker({
      commit
    }, para) {
      commit('_opendatepicker', para)
    },
    getuserinfobyid({ //根据用户id查找用户信息
      commit
    }) {
      Vue.prototype.$http('/users/getuserinfobyid', {
        uid: localStorage.getItem('uid')
      }, rs => {
        commit('_getuserinfobyid', rs);
      })
    },
    getmissionbyid({ //根据mid（任务ID）获取任务详情
      commit
    }, para) {
      Vue.prototype.$http('/getmissionbyid', {
        mid: localStorage.getItem('mid')
      }, rs => {
        commit('_getmissionbyid', rs);
      })
    },
    getprogress({ //获取回复进度
      commit
    }, para) {
      console.log('nowmid', localStorage.getItem('mid'))
      Vue.prototype.$http('/gethsinfo', {
        mid: localStorage.getItem('mid')
      }, rs => {
        commit('_getprogress', rs);
      })
    },
    saveprogress({ //保存回复内容
      commit
    }, para) {
      var touid = this.state.qwe.missioninfo.authorid;
      Vue.prototype.$http('/saveinfo', {
        maid: localStorage.getItem('maid'),
        content: para,
        touid: touid,
        mid: localStorage.getItem('mid'),
        uid: localStorage.getItem('uid'),
      }, rs => {
        this._actions['qwe/getprogress'][0]({
          mid: localStorage.getItem('mid')
        })
        Vue.prototype.socket.emit('msg2');
      })
    },
    setdocumentwidth({
      commit
    }, para) {
      commit('getdocumentwidth', para);
    },
    show_leftbar({
      commit
    }) {
      commit('leftbar', true);
    },
    hide_leftbar({
      commit
    }) {
      commit('leftbar', false);
    },
    chooseop({
      commit
    }, para) {
      commit('_chooseop', para);
    },
    getrilidata({
      commit
    }, para) {
      Vue.prototype.$http('/getPlanByMonth', {
        uid: localStorage.getItem('uid'),
        time: localStorage.getItem('nowmonth'),
        px: para
      }, rs => {
        commit('_getrilidata', rs);
      })
    },
    getuserlist({
      commit
    }, para) {
      Vue.prototype.$http('/users/getUserList', {}, rs => {
        commit('setuserlist', rs);
      })
    },
    showeditor({
      commit
    }, para) {
      console.log(this)
      commit("_showeditor", para)
    },
    missioncommit({
      commit
    }, para) {
      Vue.prototype.$http('/save', para, rs => {
        commit('_missioncommit', para.authorid)
        Vue.prototype.socket.emit('msg2');
      })
    },
    settime({
      commit
    }, para) {
      commit('_settime', para)
    },
    msgbtn({
      commit
    }, para) {
      commit('_msgbtn', para)

      if (para.q === "finishmission") {
        Vue.prototype.$http('/updateMission', para.para, rs => {
          this._actions["qwe/getmissionbyid"][0]({
            mid: localStorage.getItem('mid')
          });
        })
      }
    },
    showmsg({
      commit
    }, para) {
      commit('_showmsg', para)
    },
    updateicon({
      commit
    }, para) {

      Vue.prototype.$http('/users/updateicon', para, rs => {
        commit('_updateicon')
      })
    }
  },
  mutations: {
    _getmsgnum(state, para) {
      if (para && para.length > 0) {
        state.msgnum = para[0].num * 1
      }
    },
    _gethuoyue(state, para) {
      state.chart.gethuoyue = para
    },
    _getpostnum_c(state, para) {
      state.chart.getpostnum_c = para
    },
    _closedatepicker(state) {
      state.datepicker = false;
    },
    _opendatepicker(state, para) {
      state.setwhichtime = para;
      state.datepicker = true;
    },
    _updateicon(state) {
      { //提交任务，重新调用接口取得新数据
        this._actions["qwe/getuserinfobyid"][0]({
          uid: localStorage.getItem('uid')
        });
      } { //显示成功弹窗
        state.msg.flg = true;
        state.msg.content = '上传成功';
        state.msg.q = 0;
      }
    },
    _getuserinfobyid(state, para) {
      //console.log(para)
      //根据用户id获取用户信息
      state.userinfo = para && para.length > 0 ? para[0] : {};
    },
    _getmissionbyid(state, para) {
      //根据mid（任务ID）获得任务详情
      state.missioninfo = para[0];
    },
    _getprogress(state, para) {
      console.log('aaaaaaaaaaaaaaaaaaaaaa', para)
      //获得任务回复进度
      state.progressinfo = para;
    },
    _showmsg(state, para) {
      //显示弹窗
      state.msg.flg = true;
      state.msg.content = para.text;
      state.msg.q = para.q;
      state.msg.para = para.para;
    },
    _msgbtn(state, para) {
      //关闭弹窗
      state.msg.flg = false;
    },
    _settime(state, para) {
      if (state.setwhichtime * 1 === 1) {
        if (para < localStorage.getItem('nowday')) {
          state.msg.flg = true;
          state.msg.content = '期望完成日期不可在今天之前';
        } else {
          //console.log(state.editor.time2 && para > state.editor.time2)
          if (state.editor.time2 && para > state.editor.time2) {
            state.msg.flg = true;
            state.msg.content = '最后期限不可在期望日期之前';
          } else {
            state.editor.time1 = para;
            state.datepicker = false;
          }
        }
      } else if (state.setwhichtime * 1 === 2) {
        if (para < localStorage.getItem('nowday')) {
          state.msg.flg = true;
          state.msg.content = '最后期限不可在今天之前';
        } else {
          if (state.editor.time1 && para < state.editor.time1) {
            state.msg.flg = true;
            state.msg.content = '最后期限不可在期望日期之前';
          } else {
            state.editor.time2 = para;
            state.datepicker = false;
          }
        }
      }
    },
    _missioncommit(state) {
      { //提交任务，初始化文本编辑器
        state.editor.showflg = false;
        state.editor.time1 = '';
        state.editor.time2 = '';
      } { //提交任务，重新调用接口取得新数据
        this._actions["qwe/getrilidata"][0]({
          uid: localStorage.getItem('uid'),
          time: localStorage.getItem('nowmonth')
        });
      } { //显示成功弹窗
        state.msg.flg = true;
        state.msg.content = '保存成功';
        state.msg.q = 0;
      }
    },
    _showeditor(state, para) {
      state.editor.time1 = '';
      state.editor.time2 = '';
      //文本编辑器是够显示的flg
      state.editor.showflg = para;
    },
    setuserlist(state, para) {
      //设置选中人员的list
      state.userlist = para;
    },
    leftbar(state, para) {
      state.leftbar_show = para;
    },
    getdocumentwidth(state, para) {
      if (para > 1000) {
        state.leftbar_show = true;
      }
      state.documentwidth = para;
    },
    _chooseop(state, para) {
      { //初始化文本编辑器
        state.editor.showflg = false;
        state.editor.time1 = '';
        state.editor.time2 = '';
      }

      state.leftbar.nowidx = para;
      if (state.documentwidth <= 1000) {
        state.leftbar_show = false;
      }
    },
    _getrilidata(state, para) {
      var info = para[0];
      var datap = [];
      if (info) {
        var getlist = info.getlist;
        var postlist = info.postlist;

        if (getlist && getlist.length > 0) { //接收到的任务列表
          for (var i = 0; i < getlist.length; i++) {
            var _temp = getlist[i];
            _temp.az = 'getlist';
            datap[_temp.updatetime.substring(0, 10)] ? '' : datap[_temp.updatetime.substring(0, 10)] = [];
            datap[_temp.updatetime.substring(0, 10)].push(_temp)
          }
        }

        if (postlist && postlist.length > 0) { //发出的任务列表
          for (var i = 0; i < postlist.length; i++) {
            var _temp = postlist[i];
            _temp.az = 'postlist';
            datap[_temp.updatetime.substring(0, 10)] ? '' : datap[_temp.updatetime.substring(0, 10)] = [];
            datap[_temp.updatetime.substring(0, 10)].push(_temp)
          }
        }
      }
      state.rili.data = datap;
    }
  }
}



