<template>
  <chart :options="option" class="c" ref='c'>
  </chart>
</template>

<script>
  import {
    mapState,
    mapActions
  } from 'vuex'

  import Echarts from "vue-echarts";

  export default {
    name: 'huoyue',
    components: {
      chart: Echarts
    },
    mounted() {
      this.gethuoyue();
    },
    methods: {
      ...mapActions({
        gethuoyue: 'qwe/gethuoyue'
      }),
      getdaycount(year, month) {
        var day = new Date(year, month, 0);
        var daycount = day.getDate();
        return daycount;
      },
      add0(num) {
        return num < 10 ? "0" + num : num;
      }
    },
    computed: {
      ...mapState('qwe', {
        option(state) {
          var t = new Date();
          var daynum = this.getdaycount(t.getFullYear(), t.getMonth() + 1);

          var p = [];
          var timelist = [];
          var midlist = [];
          var data = [];
          if (state.chart && state.chart.gethuoyue && state.chart.gethuoyue.length > 0) {
            var info = state.chart.gethuoyue;
            for (var i = 0; i < info.length; i++) {
              var _info = info[i];
              p[_info.time + '-' + _info.mid] = _info;
              midlist.push(_info.mid);
            }


            for (var i = 0; i < daynum; i++) {
              var time =
                t.getFullYear() +
                "-" +
                this.add0(t.getMonth() + 1) +
                "-" +
                this.add0(i + 1);
              timelist.push(time);
              for (var ii = 0; ii < midlist.length; ii++) {
                data.push([i, ii, p[time + '-' + midlist[ii]] ? p[time + '-' + midlist[ii]].count * 1 : '-'])
              }

            }
          }

          this.c.xAxis.data = timelist;
          this.c.yAxis.data = midlist;
          this.c.series[0].data = data;

          return this.c;
        }
      })
    },
    data() {
      return {
        c: {
          title: {
            text: '本月任务回复热力',
            textStyle: {
              //文字颜色
              color: '#555',
              //字体风格,'normal','italic','oblique'
              fontStyle: 'normal',
              //字体粗细 'normal','bold','bolder','lighter',100 | 200 | 300 | 400...
              fontWeight: '300',
            },
            left: 'center'
          },
          tooltip: {
            position: 'top',
            formatter(para) {
              return para.name + '回复' + para.data[2] + '条';
            }
          },
          animation: false,
          grid: {
            height: '50%',
            y: '10%'
          },
          xAxis: {
            type: 'category',
            data: [],
            splitArea: {
              show: true
            }
          },
          yAxis: {
            type: 'category',
            data: [1, 2, 3],
            splitArea: {
              show: true
            }
          },
          visualMap: {
            min: 0,
            max: 10,
            calculable: true,
            orient: 'horizontal',
            left: 'center',
            bottom: '15%'
          },
          series: [{
            type: 'heatmap',
            data: [],
            label: {
              normal: {
                show: true
              }
            },
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }]
        }
      }
    }
  }

</script>

<style scoped>
  .c {
    width: 100%;
  }

</style>



