<template>
  <div class="loginc">
    <div class="inpc">
      <i class="iconfont icon-account"></i>
      <input v-model="username" type="text" style="margin-bottom: -1px;">
    </div>
    <div class="inpc">
      <i class="iconfont icon-password"></i>
      <input v-model="password" type="password" v-on:keyup.enter="login">
    </div>
    <!-- <div class="errinfo">{{errinfo}}</div> -->
    <div>
      <button @click="login">登 录</button>
    </div>
  </div>
</template>


<script>
  import {
    mapState,
    mapActions
  } from "vuex";


  export default {
    name: "loginc",
    data() {
      return {
        username: "",
        password: "",
        errinfo: "."
      };
    },
    methods: {
      login() {
        console.log('qwerqwe');
        this.$router.push("/RiLi");
        return;
        this.$http('/users/login', {
          uname: this.username,
          password: this.password
        }, rs => {
          if (rs && rs.length > 0) {
            {
              localStorage.setItem("uid", rs[0].uid)
              //this.socket.emit('login', rs[0].uid);
            } {
              localStorage.removeItem('nowmonth');
              localStorage.removeItem('nowday');
              localStorage.removeItem('maid');
              localStorage.removeItem('mid');
            }
            this.$router.push("/RiLi");
          } else {
            //this.errinfo = rs.msg;
            this.showmsg({
              text: '账户名或密码错误！',
              q: 0
            })
          }
        })
      },
      ...mapActions({
        showmsg: "qwe/showmsg"
      })
    }
  };

</script>

<style scoped>
  .errinfo {
    color: red;
    font-weight: 300;
    text-align: center;
  }

  .inpc {
    position: relative;
  }

  .inpc i {
    position: absolute;
    top: 8px;
    left: 8px;
    color: #c700ff;
  }

  button {
    width: 100%;
    height: 35px;
    background: #0d91ca;
    color: #fff;
    border: 0px;
    margin-top: 20px;
    cursor: pointer;
    font-size: 18px;
    border-radius: 2px;
    transition: background 0.2s ease;
  }

  button:hover {
    background: #1472a4;
  }

  input {
    height: 35px;
    width: 100%;
    border: 1px solid #ccc;
    border-radius: 0px;
    outline: none;
    font-size: 15px;
    font-weight: 300;
    text-indent: 30px;
    transition: border-right 0.2s ease;
    -webkit-appearance: none;
  }

  input:focus {
    border-right: 2px solid #0e7fb0;
  }

  .loginc {
    width: 300px;
  }

</style>



