<template>
  <div>
    <transition name="fade">
      <div class="back" v-if="showflg">
        <div class="content">
          <div class="closebtn" @click="showflg=false">×</div>
          <div class="title">头像</div>
          <croppa v-model="myCroppa"></croppa>
          <div>
            <button @click="getimg">上传</button>
          </div>
        </div>
      </div>
    </transition>

    <!-- <router-link to="/test"><button>qwe</button></router-link> -->
    <div class="personcon">
      <div class="perlist">
        <span>头像</span>
        <img :src="userinfo.icon" alt="" @click="showflg=true">
      </div>
      <div class="perlist">
        <span>序号</span>{{userinfo.uid}}</div>
      <div class="perlist">
        <span>姓名</span>{{userinfo.uname}}</div>
    </div>
  </div>
</template>

<script>
  import {
    mapState,
    mapActions
  } from "vuex";
  export default {
    name: "personal",
    data() {
      return {
        myCroppa: {},
        showflg: false,
      };
    },
    computed: {
      ...mapState('qwe', {
        userinfo: state => state.userinfo
      })
    },
    mounted() {
      this.getuserinfobyid()
    },
    methods: {
      uploadCroppedImage() {
        this.myCroppa.generateBlob(
          blob => {
            //console.log(blob);
            // write code to upload the cropped image file (a file is a blob)
          },
          "image/jpeg",
          0.2
        ); // 80% compressed jpeg file
      },
      getimg() {
        if (this.myCroppa.generateDataUrl("image/jpeg", 0.1).length > 0) {
          this.showflg = false;
          this.updateicon({
            uid: this.userinfo.uid,
            icon: this.myCroppa.generateDataUrl("image/jpeg", 0.1)
          });
        } else {
          this.showmsg({
            text: "请选择图片",
            q: 0
          });
        }
      },
      ...mapActions({
        updateicon: "qwe/updateicon",
        showmsg: "qwe/showmsg",
        getuserinfobyid: 'qwe/getuserinfobyid',
      })
    },
  };

</script>


<style scoped>
  .fade-enter-active,
  .fade-leave-active {
    transition: opacity .2s;
  }

  .fade-enter,
  .fade-leave-active {
    opacity: 0;
  }

  div {
    word-break: break-all;
  }

  .perlist span {
    font-weight: normal;
    margin-right: 20px;
  }

  .perlist img {
    width: 60px;
    height: 60px;
    border-radius: 60px;
    cursor: pointer;
  }

  .perlist {
    border-bottom: 1px solid #d6d6d6;
    padding: 10px 0px;
  }

  .personcon {
    max-width: 1000px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin: 0 auto;
    padding: 10px;
    font-weight: 300;
  }

  .title {
    text-align: center;
    font-weight: 300;
    font-size: 14px;
    line-height: 25px;
  }

  .closebtn {
    background: #0090cc;
    width: 25px;
    height: 25px;
    position: absolute;
    right: -8px;
    top: -8px;
    text-align: CENTER;
    line-height: 20px;
    font-size: 18px;
    border: 2px solid #fff;
    color: #fff;
    border-radius: 25px;
    cursor: pointer;
    transition: background 0.2s ease;
    user-select: none;
  }

  .closebtn:hover {
    background: #1472a4;
  }

  .back {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0px;
    left: 0px;
    background: rgba(0, 0, 0, 0.4);
    z-index: 999999;
  }

  .content {
    position: absolute;
    background: #fff;
    padding: 20px;
    border-radius: 5px;
    overflow: visible;
    box-shadow: 0px 5px 5px #555;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

</style>



