<template>
  <div>
    <div class="leftbar" :style="{transform: 'translate3d('+(leftbar_show?0:-220)+'px,0,0)'}">
      <div class="title">
        <!-- <i class="iconfont icon-tree" style="font-size: 25px;color: #fff;"></i> -->
        <img src="../assets/logoo.png" alt=""> Mission Possible
      </div>
      <div class="leftcontent">
        <div v-for='item in optionlist' :key="item.idx" class="leftlist" :class="nowpath===item.route?'leftlistactive':''" @click="_chooseop(item)">
          <i class="iconfont" :class="item.icon"></i>
          {{item.name}}
          <div class="contentnum" v-if="item.num&&item.num>0">{{item.num}}</div>
        </div>
      </div>
    </div>
    <transition name="fade">
      <div class="leftbarback" v-show="leftbar_show&&documentwidth<1000" @click="hide_leftbar"></div>
    </transition>
  </div>
</template>

<script>
  import {
    mapState,
    mapActions
  } from "vuex";
  export default {
    name: "leftbar",
    computed: {
      ...mapState("qwe", {
        leftbar_show: state => state.leftbar_show,
        documentwidth: state => state.documentwidth,
        nowidx: state => state.leftbar.nowidx,
        optionlist(state) {
          this.c[1].num = state.msgnum;
          return this.c;
        }
      })
    },
    methods: {
      ...mapActions({
        show_leftbar: "qwe/show_leftbar",
        hide_leftbar: "qwe/hide_leftbar",
        chooseop: "qwe/chooseop",
        getmsgnum: "qwe/getmsgnum"
      }),
      _chooseop(item) {
        this.$router.push(item.route);
        this.chooseop(item.idx);
        this.nowpath = window.location.hash.split('#')[1];
      },
      // getmsgnum() {
      //   this.$http('/getmsgnum', {
      //     uid: localStorage.getItem('uid'),
      //   }, rs => {
      //     console.log('``````````````', rs)
      //     if (rs && rs.length > 0) {
      //       this.optionlist[1].num = rs[0].num * 1
      //     }
      //   })
      // }
    },
    mounted() {
      this.nowpath = window.location.hash.split('#')[1];
      this.getmsgnum();

      this.socket.on('msg', obj => {

        this.getmsgnum();
      })
    },
    data() {
      return {
        nowpath: '',
        c: [{
            name: "任务日历",
            icon: "icon-rili",
            route: '/RiLi'
          },
          {
            name: "通知中心",
            icon: "icon-tongzhi",
            num: 0,
            route: '/MessageCenter'
          },
          {
            name: "用户列表",
            icon: "icon-yonghuquntiliebiao",
            route: '/UserManager'
          },
          {
            name: "个人中心",
            icon: "icon-geren01",
            route: '/Personal'
          },
          {
            name: "任务分析",
            icon: "icon-tubiao",
            route: '/Charts'
          },
          // {
          //   name: "测试",
          //   icon: "icon-electronics",
          //   route: '/test'
          // }
          // { name: 'Add Mission', idx: 2, icon: 'icon-edit' },
          // { name: 'My', idx: 3, icon: 'icon-account' },
        ]
      };
    }
  };

</script>

<style scoped>
  .contentnum {
    position: absolute;
    left: 190px;
    top: 15px;
    background: #af2de4;
    width: 20px;
    height: 20px;
    border-radius: 20px;
    color: #fff;
    text-align: center;
    line-height: 22px;
    font-size: 14px;
  }

  .leftcontent {
    height: calc(100vh - 60px);
    border-right: 1px solid #ccc;
  }

  .leftlist:hover {
    background: #e5e5e5;
  }

  .leftlistactive {
    border-right: 3px solid #234 !important;
    color: #ae2ee1;
  }

  .leftlist {
    position: relative;
    height: 50px;
    line-height: 50px;
    font-size: 15px;
    font-weight: 300;
    cursor: pointer;
    padding-left: 35px;
    transition: border 0.1s ease;
  }

  .title img {
    width: 60px;
    height: 60px;
    float: left;
  }

  .title {
    height: 60px;
    background: #eee;
    height: 60px;
    background: #000;
    font-size: 20px;
    font-weight: 300;
    line-height: 60px;
    text-align: center;
    color: #fff;
  }

  .leftbarback {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.3);
    z-index: 99999;
  }

  .leftbar {
    width: 220px;
    height: 100vh;
    position: absolute;
    transition: transform 0.2s ease;
    z-index: 999999;
    background: #fff;
  }

  .fade-enter-active,
  .fade-leave-active {
    transition: opacity 0.5s;
  }

  .fade-enter,
  .fade-leave-active {
    opacity: 0;
  }

</style>



