<template>
  <div>
    <div class="content">
      <div class="title">Mission Possible</div>
      <div class="subtitle">Powered by Flower</div>
      <loginc></loginc>
      <div class="alarmtitle" @click="zhuce">注册</div>
    </div>

    <transition name="fade">
      <div class="back" v-if="zhuceflg">
        <div class="logincc">
          <div class="closebtn" @click="zhuceflg=false">×</div>
          <div class="inpcc">
            <i class="iconfont icon-account"></i>
            <input v-model="username" type="text" style="margin-bottom: -1px;">
          </div>
          <div class="inpcc">
            <i class="iconfont icon-password"></i>
            <input v-model="password" type="password" v-on:keyup.enter="register">
          </div>
          <!-- <div class="errinfo">{{errinfo}}</div> -->
          <div>
            <button @click="register">注 册</button>
          </div>
        </div>
      </div>
    </transition>

    <Msg></Msg>
  </div>
</template>

<script>
  import {
    mapState,
    mapActions
  } from "vuex";

  import Msg from "./components/Msg";

  import loginc from "./components/loginc";
  export default {
    name: "Login",
    methods: {
      ...mapActions({
        showmsg: "qwe/showmsg"
      }),
      zhuce() {
        this.zhuceflg = true;
      },
      register() {
        this.$http('/users/register', {
          uname: this.username,
          password: this.password
        }, rs => {
          if (rs.msg) {
            this.showmsg({
              text: rs.msg,
              q: 0
            })
          } else {
            this.zhuceflg = false;
            this.showmsg({
              text: '注册成功！',
              q: 0
            })
          }
        })
      }
    },
    data() {
      return {
        zhuceflg: false,
        username: '',
        password: ''
      }
    },
    components: {
      loginc,
      Msg
    }
  };

</script>


<style scoped>
  .fade-enter-active,
  .fade-leave-active {
    transition: opacity .2s;
  }

  .fade-enter,
  .fade-leave-active {
    opacity: 0;
  }

  .closebtn {
    background: #0090cc;
    width: 25px;
    height: 25px;
    position: absolute;
    right: -8px;
    top: -8px;
    text-align: CENTER;
    line-height: 20px;
    font-size: 18px;
    border: 2px solid #fff;
    color: #fff;
    border-radius: 25px;
    cursor: pointer;
    transition: background 0.2s ease;
    user-select: none;
  }

  .closebtn:hover {
    background: #1472a4;
  }

  .back {
    width: 100%;
    height: 100%;
    position: fixed;
    background: rgba(0, 0, 0, 0.4);
  }

  .inpcc {
    position: relative;
  }

  .inpcc i {
    position: absolute;
    top: 8px;
    left: 8px;
    color: #c700ff;
  }

  button {
    width: 100%;
    height: 35px;
    background: #0d91ca;
    color: #fff;
    border: 0px;
    margin-top: 20px;
    cursor: pointer;
    font-size: 18px;
    border-radius: 2px;
    transition: background 0.2s ease;
  }

  button:hover {
    background: #1472a4;
  }

  input {
    height: 35px;
    width: 100%;
    border: 1px solid #ccc;
    border-radius: 0px;
    outline: none;
    font-size: 15px;
    font-weight: 300;
    text-indent: 30px;
    transition: border-right 0.2s ease;
    -webkit-appearance: none;
  }

  input:focus {
    border-right: 2px solid #0e7fb0;
  }

  .logincc {
    width: 300px;
    width: 300px;
    background: #fff;
    padding: 20px 10px;
    border-radius: 5px;
    box-shadow: 0px 5px 5px #555;
    position: absolute;
    transform: translate(-50%, -50%);
    left: 50%;
    top: 50%;
  }

  .content {
    width: 300px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -70%);
  }

  .title {
    margin: 15px;
    text-align: center;
    font-size: 30px;
    font-weight: 300;
    user-select: none;
  }

  .subtitle {
    margin: 5px;
    text-align: center;
    font-size: 15px;
    font-weight: 300;
    user-select: none;
    color: #888;
  }

  .alarmtitle {
    margin: 5px;
    text-align: center;
    font-size: 15px;
    font-weight: 300;
    color: #ff9300;
    transition: color 0.2s ease;
    text-decoration-line: underline;
    cursor: pointer;
  }

  .alarmtitle:hover {
    color: #cf8520;
  }

</style>



