<template>
  <div>

    <Leftbar></Leftbar>
    <transition name="fade">
      <Datepicker v-if="datepicker"></Datepicker>

    </transition>

    <transition name="fade">
      <Editor v-if='showflg'></Editor>

    </transition>

    <Msg></Msg>
    <div class="maincontent" :style="{width: 'calc(100% - '+(documentwidth<=1000?0:220)+'px)'}">
      <div class="menu">
        <div class="listicon" @click="show_leftbar" v-show="documentwidth<=1000">
          <i class="iconfont icon-category" style="font-size: 25px;color:#fff;"></i>
        </div>
        <!-- <div class="searchbox">
          <div class="searchbtn">
            <i class="iconfont icon-search1" style="font-size: 20px"></i>
          </div>
          <input type="text">
        </div> -->
        <div class="logout" @click='logout'>
          <i class="iconfont icon-exit"></i>
          登出
        </div>
      </div>

      <div style="width:100%;height:calc(100vh - 62px);overflow:auto;-webkit-overflow-scrolling: touch;">
        <div class="contentbody">
          <router-view ref='r' />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {
    mapState,
    mapActions
  } from "vuex";
  import Leftbar from "./components/Leftbar";
  import RiLi from "./components/RiLi";
  import Tablec from "./components/Tablec";
  import Personal from "./components/Personal";
  import UserManager from "./components/UserManager";
  import Msg from "./components/Msg";
  import Missioninfo from "./components/Missioninfo";
  import Datepicker from './components/Datepicker.vue';
  import Editor from './components/Editor.vue'

  export default {
    name: "main",
    components: {
      Leftbar,
      RiLi,
      Tablec,
      Personal,
      UserManager,
      Msg,
      Missioninfo,
      Datepicker,
      Editor
    },
    created() {
      this.setdocumentwidth(document.body.clientWidth);

      // const that = this
      // this.socket = io.connect('http://localhost:4000');
      // this.socket.on('message', function (obj) {
      //   window.scrollTo(0, 900000)
      // })
      // this.socket.on('logout', function (obj) {
      //   that.$store.commit('setusers', obj)
      // })

    },
    computed: {
      ...mapState("qwe", {
        documentwidth: state => state.documentwidth,
        nowidx: state => state.leftbar.nowidx,
        topidx: state => state.topidx,
        datepicker: state => state.datepicker,
        showflg: state => state.editor.showflg
      })
    },
    methods: {
      ...mapActions({
        show_leftbar: "qwe/show_leftbar",
        hide_leftbar: "qwe/hide_leftbar",
        setdocumentwidth: "qwe/setdocumentwidth",
      }),
      logout() {
        this.$router.push('/')
      }
    },
    mounted() {
      var _this = this;

      console.log('main', this)
      window.onresize = function () {
        _this.setdocumentwidth(document.body.clientWidth);
        setTimeout(function () {
          if (_this.$refs.r && _this.$refs.r.$refs.q1 && _this.$refs.r.$refs.q2) {
            _this.$refs.r.$refs.q1.$refs.c.resize();
            _this.$refs.r.$refs.q2.$refs.c.resize();
          }

        }, 100);

      };
    }
  };

</script>

<style scoped>
  .fade-enter-active,
  .fade-leave-active {
    transition: opacity .2s;
  }

  .fade-enter,
  .fade-leave-active {
    opacity: 0;
  }

  .logout {
    float: right;
    color: #fff;
    margin-right: 20px;
    font-size: 15px;
    cursor: pointer;
  }

  .contentbody {
    width: 96%;
    margin: 20px auto;
  }

  .searchbtn:active {
    background: #ddd;
  }

  .searchbtn:hover {
    cursor: pointer;
  }

  .searchbtn {
    width: 35px;
    height: 35px;
    position: absolute;
    z-index: 1;
    border: 1px solid #ccc;
    right: 0px;
    top: 13px;
    border-radius: 35px;
    text-align: center;
    line-height: 35px;
    background: #fff;
  }

  .searchbox input {
    width: 100%;
    height: 35px;
    border: 1px solid #ccc;
    border-radius: 35px;
    position: absolute;
    top: 13px;
    font-size: 18px;
    color: #555;
    text-indent: 10px;
    outline: none;
  }

  .searchbox {
    width: 40%;
    max-width: 360px;
    right: 20px;
    position: absolute;
    height: 60px;
  }

  .listicon {
    float: left;
    width: 60px;
    text-align: center;
    cursor: pointer;
  }

  .maincontent {
    height: 100vh;
    float: right;
  }

  .menu {
    height: 60px;
    background: #000;
    font-size: 20px;
    font-weight: 300;
    line-height: 60px;
  }

</style>



