<template>
  <chart :options="option" class="c" ref='c'>
  </chart>
</template>

<script>
  import {
    mapState,
    mapActions
  } from "vuex";

  import Echarts from "vue-echarts";
  export default {
    name: "charts",
    components: {
      chart: Echarts
    },
    computed: {
      ...mapState("qwe", {
        option(state) {
          console.log(state)
          var t = new Date();
          var daynum = this.getdaycount(t.getFullYear(), t.getMonth() + 1);

          var timelist = [];
          var value = [];
          var _value = [];
          var value2 = [];
          var _value2 = [];

          var info = state.chart.getpostnum_c;
          if (info && info.length > 0) {
            var cd = info[0].postdata;
            var cd2 = info[0].getdata;
            if (cd) {
              for (var i = 0; i < cd.length; i++) {
                var element = cd[i];
                //value.push(element.value);
                value[element.time] = element.value;
              }
            }

            if (cd2) {
              for (var i = 0; i < cd2.length; i++) {
                var element = cd2[i];
                // value2.push(element.value);
                value2[element.time] = element.value;
              }
            }


            for (var i = 0; i < daynum; i++) {
              var time =
                t.getFullYear() +
                "-" +
                this.add0(t.getMonth() + 1) +
                "-" +
                this.add0(i + 1);
              timelist.push(time);
              _value.push(value[time] ? value[time] : 0);
              _value2.push(value2[time] ? value2[time] : 0);
            }
          } {
            this.c.xAxis[0].data = timelist;
            this.c.series[0].data = _value;
            this.c.series[1].data = _value2;
          }

          return this.c;
        }
      })
    },
    methods: {
      ...mapActions({
        getpostnum_c: "qwe/getpostnum_c"
      }),
      getdaycount(year, month) {
        var day = new Date(year, month, 0);
        var daycount = day.getDate();
        return daycount;
      },
      add0(num) {
        return num < 10 ? "0" + num : num;
      }
    },
    mounted() {
      this.getpostnum_c();


    },
    data() {
      return {
        c: {
          title: {
            text: '本月任务收发情况',
            textStyle: {
              //文字颜色
              color: '#555',
              //字体风格,'normal','italic','oblique'
              fontStyle: 'normal',
              //字体粗细 'normal','bold','bolder','lighter',100 | 200 | 300 | 400...
              fontWeight: '300',
            },
            left: 'center'
          },
          color: ["#3398DB", "#e55b36"],
          tooltip: {
            trigger: "axis",
            axisPointer: {
              // 坐标轴指示器，坐标轴触发有效
              type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
            }
          },
          grid: {
            left: "3%",
            right: "4%",
            bottom: "3%",
            containLabel: true
          },
          xAxis: [{
            type: "category",
            data: [],
            axisTick: {
              alignWithLabel: true
            }
          }],
          yAxis: [{
            type: "value"
          }],
          series: [{
              name: "发布数量",
              type: "line",
              data: [],
              smooth: true
            },
            {
              name: "接收数量",
              type: "line",
              data: [],
              smooth: true
            }
          ]
        }
      };
    }
  };

</script>

<style scoped>
  .c {
    width: 100%;
  }

</style>



