<template>
  <div class="content">
    <div v-for="(item,i) in list" :key="i" class="list">
      <span :class="item.readflg?'readflg1':'readflg2'">【{{item.readflg?'已读':'未读'}}】</span>
      <img :src="item.icon" alt=""> {{item.uname}}
      <span class="time">于-{{item.updatetime}}</span>{{formattertype(item.type)}}，
      <span class="link" @click="gotoUrl(item)">去看看吧</span>
    </div>
    <div class="zanwu" v-if="!list">
      <i class="iconfont icon-zanwu"></i> 暂无通知</div>
  </div>
</template>

<script>
  import {
    mapState,
    mapActions
  } from "vuex";

  export default {
    name: "usermanager",
    data() {
      return {
        list: []
      }
    },
    methods: {
      ...mapActions({
        getmsgnum: "qwe/getmsgnum"
      }),
      getmsglist() {
        this.$http('/getmsglist', {
          uid: localStorage.getItem('uid'),
        }, rs => {
          this.list = rs;
        })
      },
      formattertype(type) {
        if (type * 1 === 0) {
          return '向你发布了一条新任务'
        } else if (type * 1 === 1) {
          return '回复了你'
        } else if (type * 1 === 2) {
          return '回复了你'
        }
      },
      gotoUrl(item) {
        localStorage.setItem('mid', item.mid);
        this.$http('/updatemsg', {
          id: item.id
        }, rs => {
          this.getmsgnum();
          this.$router.push('/Missioninfo');
        })
      }
    },
    mounted() {
      this.getmsglist();
    }
  };

</script>

<style scoped>
  .zanwu i {
    font-size: 30px;
  }

  .zanwu {
    font-size: 15px;
    font-weight: 300;
  }

  .readflg2 {
    color: red;
  }

  .readflg1 {
    color: green;
  }

  .list img {
    width: 40px;
    height: 40px;
    border-radius: 40px;
    float: left;
  }

  .time {
    font-size: 13px;
    color: #ba8cd6;
  }

  .link:hover {
    color: #1f02ff
  }

  .link {
    color: #2484ea;
    text-decoration-line: underline;
    cursor: pointer;
  }

  .list {
    border-bottom: 1px solid #ccc;
    padding: 10px 0px;
    font-weight: 300;
    font-size: 14px;
    position: relative;
    line-height: 40px;
    text-indent: 10px;
  }

</style>



