<template>
  <div class="content">
    <table>
      <thead>
        <tr>
          <th style="width: 10%;">用户icon</th>
          <th style="width: 10%;">用户id</th>
          <th>用户名</th>
          <th style="width: 10%;">操作</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item,i) in userlist" :key="i" >
          <td><img style="width: 60px;height: 60px;border-radius: 60px;" :src="item.icon" alt=""></td>
          <td>{{item.uid}}</td>
          <td>{{item.uname}}</td>
          <td>-</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "usermanager",
  computed: {
    ...mapState("qwe", {
      userlist: state => state.userlist,
      documentwidth: state => state.documentwidth
    })
  },
  methods: {
    ...mapActions({
      getuserlist: "qwe/getuserlist"
    })
  },
  mounted() {
    this.getuserlist();
    //console.log(this.documentwidth);
  }
};
</script>

<style scoped>
table {
  width: 100%;
}

th {
  font-weight: 300;
  padding: 6px;
  font-size: 14px;
}

td {
  padding: 10px;
  margin: 2px;
  text-align: center;
  font-weight: 300;
  font-size: 13px;
}

tbody tr {
  background: #eee;
  cursor: pointer;
}

tbody tr:hover {
  background: #e0dff3;
}
</style>





