<template>
  <div>
    <div class="title">DEVICE-W:@-{{documentwidth}}</div>
    <q1 ref='q1'></q1>
    <q2 ref='q2'></q2>
  </div>
</template>

<script>
  import {
    mapState,
    mapActions
  } from 'vuex'

  import q1 from "./Charts/本月任务收发情况";
  import q2 from './Charts/任务活跃情况';

  export default {
    name: "charts",
    components: {
      q1,
      q2,
    },
    computed: {
      ...mapState("qwe", {
        documentwidth: state => state.documentwidth
      })
    },
    mounted() {
      //this.$refs.q1.$refs.c.resize();
    }
  };

</script>

<style scoped>
  .title {
    font-weight: 300;
  }

</style>



