<template>
  <div style="overflow: hidden">
    <div id="editorElem" style="text-align:left"></div>
    <div style="margin-top:10px">
      <button class="commitbtn" @click="getContent">提交</button>
    </div>
  </div>
</template>

<script>
  import E from "wangeditor";
  import {
    mapState,
    mapActions
  } from "vuex";

  var editor;
  export default {
    name: "editor",
    computed: {
      ...mapState("qwe", {
        qwe: state => state.missioninfo
      })
    },
    methods: {
      ...mapActions({
        saveprogress: "qwe/saveprogress"
      }),
      getContent() {
        this.saveprogress(this.editorContent);
        editor.txt.html('')
      }
    },
    data() {
      return {
        editorContent: ""
      };
    },
    mounted() {
      editor = new E("#editorElem");
      editor.customConfig.uploadImgServer = "http://www.mobius-vision.com:4000/qwe?uid=" + localStorage.getItem('uid');
      editor.customConfig.menus = [
        "head", // 标题
        "strikeThrough", // 删除线
        "quote", // 引用
        "emoticon", // 表情
        "table", // 表格
        "image",
        "code" // 插入代码
      ];
      editor.customConfig.onchange = html => {
        this.editorContent = html;
      };
      editor.create();
    }
  };

</script>

<style scoped>
  .wrboxd {
    overflow: hidden;
    margin-bottom: 20px;
    position: relative;
  }

  .plactive {
    background: #cf7f7f !important;
    color: #fff;
  }

  .pstyactive {
    background: #af68c2 !important;
    color: #fff;
  }

  .psty:hover {
    background: #ddd;
  }

  .psty {
    margin: 5px;
    padding: 5px 10px;
    background: #eee;
    float: left;
    border-radius: 5px;
    cursor: pointer;
  }

  .lititle {
    border-bottom: 1px solid #ccc;
  }

  .wrbox {
    float: left;
  }

  .errorinfo {
    float: left;
    color: red;
  }

  input {
    height: 40px;
    width: 100%;
    border: 1px solid #ccc;
    border-radius: 0px;
    outline: none;
    font-size: 18px;
    font-weight: 300;
    -webkit-appearance: none;
    margin-bottom: -1px;
  }

  .commitbtn {
    width: 100px;
    float: right;
  }

</style>



