<template>
  <div>
    <div class="toolbar">
      <!-- <div class="backbtn" onclick="history.back()">
        <i class="iconfont icon-back"></i>返回-任务日历</div> -->
      <div style="text-indent:10px;float:left;">
        任务列表</div>
      <div class="paixubtn">
        <div @click="showpxlist">
          <i class="iconfont icon-Sort"></i>
          {{nowpxname}}
        </div>
        <div class="paixucontent" v-show="pxflg">
          <div v-for="(item,i) in pxlist" :key="i" @click="pxfunc(item)">{{item}}</div>
        </div>
      </div>
    </div>
    <div class="title">
      <i class="iconfont icon-emailfilling"></i> 我收到的任务</div>
    <div class="tbcontent">
      <table v-if="getlist.length>0">
        <thead>
          <tr>
            <th>任务ID</th>
            <th>任务标题</th>
            <th>发布人</th>
            <th>优先级</th>
            <th>发布时间</th>
            <th>期望时间</th>
            <th>最后期限</th>
            <th>可用时间</th>
            <th>任务状态</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item,i) in getlist" :key="i" @click.stop="_setmissioninfo(item)">
            <td :class="item.state==='进行中'?'blue':item.state==='已完成'?'green':'cheng'">{{item.mid}}</td>
            <td>{{item.title}}</td>
            <td>{{item.uname}}</td>
            <td :class='getbgcolor(item.pl)'>{{formatepl(item.pl)}}</td>
            <td>{{item.updatetime}}</td>
            <td>{{item.time1?item.time1:'-'}}</td>
            <td>{{item.time2}}</td>
            <td>{{item.lifttime==='00:00:00'?'1day':item.lifttime}}</td>
            <td>{{item.state}}</td>
          </tr>
        </tbody>
      </table>
      <div v-else class="subtitle">
        <i class="iconfont icon-cry"></i> No data</div>
    </div>

    <div class="title" style="margin-top: 40px;">
      <i class="iconfont icon-emailfilling"></i> 我发出的任务
      <div class="fucbtn" style="top: 0px;right: 0px;" @click="showeditor(true)" v-if="nowdate()===time">+新建任务</div>
      <!-- <div style="margin-top: 10px;font-size:15px;" v-if="showflg">
        
      </div> -->
    </div>
    <div class="tbcontent">
      <table v-if="postlist.length>0">
        <thead>
          <tr>
            <th>任务ID</th>
            <th>任务标题</th>
            <th>发布人</th>
            <th>优先级</th>
            <th>发布时间</th>
            <th>期望时间</th>
            <th>最后期限</th>
            <th>可用时间</th>
            <th>任务状态</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item,i) in postlist" :key="i" @click.stop="_setmissioninfo(item)">
            <td :class="item.state==='进行中'?'blue':item.state==='已完成'?'green':'cheng'">{{item.mid}}</td>
            <td>{{item.title}}</td>
            <td>我</td>
            <td :class='getbgcolor(item.pl)'>{{formatepl(item.pl)}}</td>
            <td>{{item.updatetime}}</td>
            <td>{{item.time1?item.time1:'-'}}</td>
            <td>{{item.time2}}</td>
            <td>{{item.lifttime==='00:00:00'?'1day':item.lifttime}}</td>
            <td>{{item.state}}</td>
          </tr>
        </tbody>
      </table>
      <div v-else class="subtitle">
        <i class="iconfont icon-cry"></i> No data
      </div>
    </div>

  </div>
</template>

<script>
  import {
    mapState,
    mapActions,
  } from "vuex";

  import Editor from "./Editor";

  export default {
    name: "table",
    components: {
      Editor
    },
    data() {
      return {
        pxflg: false,
        pxlist: ["默认排序", "优先级排序", "临近日排序", "完成状态排序"],
        nowpxname: "默认排序",
        time: localStorage.getItem('nowday')
      };
    },
    computed: {
      ...mapState("qwe", {
        showflg: state => state.editor.showflg,
        getlist(state) {
          //console.log('getlist-state', state)
          var p = [];
          if (state.rili.data) {
            var info = state.rili.data[localStorage.getItem('nowday')];
            if (info && info.length) {
              for (var i = 0; i < info.length; i++) {
                var _info = info[i];
                if (_info.az === "getlist") {
                  p.push(_info);
                }
              }
            }
          }
          //console.log("getlist", p);
          return p;
        },
        postlist(state) {
          var p = [];
          if (state.rili.data) {
            var info = state.rili.data[localStorage.getItem('nowday')];
            if (info && info.length) {
              for (var i = 0; i < info.length; i++) {
                var _info = info[i];
                if (_info.az === "postlist") {
                  p.push(_info);
                }
              }
            }
          }
          //console.log("postlist", p);
          return p;
        },
      })
    },
    methods: {
      _setmissioninfo(item) {
        if (item.maid) {
          localStorage.setItem('maid', item.maid)
        } else {
          localStorage.removeItem('maid')
        }
        localStorage.setItem('mid', item.mid)
        this.$router.push('/Missioninfo');
      },
      pxfunc(name) {
        this.nowpxname = name;
        this.pxflg = false;
        this.getrilidata(this.formatepx(this.nowpxname));
      },
      showpxlist() {
        this.pxflg = !this.pxflg;
      },
      ...mapActions({
        chooseop: "qwe/chooseop",
        showeditor: "qwe/showeditor",
        setmissioninfo: "qwe/setmissioninfo",
        getrilidata: "qwe/getrilidata"
      }),
      formatepl(i) {
        if (i === 1) {
          return "最优先";
        } else if (i === 2) {
          return "高";
        } else if (i === 3) {
          return "中";
        } else if (i === 4) {
          return "低";
        } else if (i === 5) {
          return "长期";
        }
      },
      getbgcolor(i) {
        if (i === 1) {
          return "l1";
        } else if (i === 2) {
          return "l2";
        } else if (i === 3) {
          return "l3";
        } else if (i === 4) {
          return "l4";
        }
      },
      //pxlist: ["默认排序", "优先级排序", "临近日排序", "完成状态排序"],
      formatepx(name) {
        if (name === "默认排序") {
          return "0";
        } else if (name === "优先级排序") {
          return "1";
        } else if (name === "临近日排序") {
          return "2";
        } else if (name === "完成状态排序") {
          return "3";
        }
      },
      nowdate() {
        var date = new Date();
        var time =
          date.getFullYear().toString() +
          "-" +
          this.pad2(date.getMonth() + 1) +
          "-" +
          this.pad2(date.getDate());
        return time;
      },
      pad2(n) {
        return n < 10 ? "0" + n : n;
      }
    },
    mounted() {
      this.getrilidata()
    }
  };

</script>


<style scoped>
  .tbcontent {
    overflow-x: auto;
  }

  .paixucontent div:hover {
    background: #234;
  }

  .paixucontent {
    position: absolute;
    background: #000;
    width: 100px;
    top: 39px;
    border-radius: 5px;
    text-align: center;
    overflow: hidden;
    z-index: 1;
    font-size: 12px;
    line-height: 30px;
    right: -10px;
    box-shadow: 0px 5px 5px #555;
  }

  .paixubtn {
    float: right;
    right: 10px;
    position: absolute;
    cursor: pointer;
    font-size: 15px;
  }

  .l4 {
    background: rgb(204, 239, 203);
  }

  .l3 {
    background: #d4d4ff;
  }

  .l2 {
    background: #ffd4ef;
  }

  .l1 {
    background: #ffd1d1;
  }

  .backbtn {
    position: absolute;
    left: 10px;
    cursor: pointer;
  }

  .toolbar {
    background: #000;
    color: #fff;
    height: 40px;
    line-height: 40px;
    font-weight: 300;
    border-radius: 5px;
    margin-bottom: 15px;
    position: relative;
  }

  .fucbtn {
    background: #234;
    color: #fff;
    position: absolute;
    height: 30px;
    line-height: 30px;
    padding: 0px 10px;
    border-radius: 30px;
    font-size: 13px;
    cursor: pointer;
  }

  .subtitle {
    margin-top: 20px;
    font-size: 15px;
    font-weight: 300;
    position: relative;
  }

  .title i {
    font-size: 20px;
    color: #234;
  }

  .title {
    font-size: 20px;
    font-weight: 300;
    border-bottom: 1px solid #b7d7d8;
    padding-bottom: 10px;
    position: relative;
  }

  .cheng {
    border-left: 8px solid #e99c0a;
    text-align: left;
  }

  .blue {
    border-left: 8px solid #198cc4;
    text-align: left;
  }

  .green {
    border-left: 8px solid #19c45e;
    text-align: left;
  }

  table {
    width: 100%;
  }

  th {
    font-weight: 300;
    padding: 6px;
    font-size: 14px;
  }

  td {
    padding: 10px;
    margin: 2px;
    text-align: center;
    font-weight: 300;
    font-size: 13px;
  }

  tbody tr {
    background: #eee;
    cursor: pointer;
  }

  tbody tr:hover {
    background: #e0dff3;
  }

</style>



