// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import Vuex from 'vuex'
import 'vue-croppa/dist/vue-croppa.css'
import Croppa from 'vue-croppa'
import io from 'socket.io-client'

Vue.use(Croppa)

Vue.use(Vuex)

var ht = 'http://172.30.9.151';
//var ht = 'http://www.mobius-vision.com';

Vue.prototype.socket = io.connect(ht + ':4001');

//Vue.prototype.$http = axios
Vue.prototype.$http = function (url, para, callback) {
  //var domain = 'http://www.mobius-vision.com:4000';
  var domain = ht + ':4000';
  //console.log(domain + url, para)
  axios({
    method: 'post',
    url: domain + url,
    data: para
  }).then(rs => {
    //console.log('mian', rs)
    if (rs.status === 200) {
      var _data = rs.data;
      if (_data.state) {
        var __data = _data.data;
        callback(__data)
      } else {
        callback({
          msg: _data.msg
        })
      }
    }
  }, rs => {
    // error callback
  })
}

Vue.config.productionTip = false


import qwe from './store'
const store = new Vuex.Store({
  modules: {
    qwe
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: {
    App
  }
})



