<template>
  <div>
    <!-- <div class="toolbar">
      <div class="backbtn" onclick="history.back()">
        <i class="iconfont icon-back"></i>
        返回-任务列表
      </div>
    </div> -->

    <div>
      <div class="conbox" :style="{width:''+(documentwidth<=1000?100:50)+'%'}">
        <div class="title">{{qwe.title}}</div>

        <div class="time">
          <img class="iconc" :src="qwe.icon" alt=""> {{qwe.uname}}
          <span style="color:rgb(156, 156, 156);font-size:14px;">提交于</span> {{qwe.updatetime}}
        </div>
        <div class="content" v-html="qwe.name"></div>
        <div>
          <div class="subtitle">任务状态：{{qwe.state}}</div>
          <div class="subtitle">优先级别：{{formatepl(qwe.pl)}}</div>
          <div class="subtitle">期望时间：{{qwe.time1?qwe.time1:'-'}}</div>
          <div class="subtitle">最后期限：{{qwe.time2}}</div>
          <div class="subtitle">可用时间：{{qwe.lifttime==='00:00:00'?'1day':qwe.lifttime}}</div>
        </div>
        <div>
          <button @click="fm" v-if="uid*1===qwe.authorid*1">结束该任务</button>
        </div>
      </div>
      <div class="conbox" :style="{width:''+(documentwidth<=1000?100:50)+'%'}">
        <div class="timelistbox">
          <div class="timelist" v-for="(item,i) in progressinfo" :key="i">
            <div class="timelisttitle">
              <img :src="item.icon" alt="">{{item.uname}}
              <span style="color:rgb(156, 156, 156);font-size:14px;">提交于</span> {{item.updatetime}}
              <button class="huifubtn" @click="huifu(item)">回复</button>
            </div>
            <div class="timelistcontent" v-html="item.content"></div>
            <div class="hfhfc" v-if="item.mpid===nowmpid">
              <div class="hfhft">
                <textarea v-model="content" name="" id="" cols="30" rows="5"></textarea>
              </div>
              <button class="hfhfbtn" @click="hfhf(item)">提交</button>
            </div>
            <div class="hflistc">
              <div class="hflist" v-for="(iitem,ii) in item.hflist" :key="ii">
                <div class="hflist1">
                  <img :src="iitem.icon" alt="">{{iitem.uname}} 提交于 {{iitem.updatetime}}</div>
                <div class="hflist2">{{iitem.content}}</div>
              </div>
            </div>
          </div>
        </div>
        <Editor2 v-if="uid*1!=qwe.authorid*1"></Editor2>
      </div>
    </div>


  </div>
</template>

<script>
  import {
    mapState,
    mapActions
  } from "vuex";
  import Editor2 from "./Editor2";

  export default {
    name: "missioninfo",
    components: {
      Editor2
    },
    data() {
      return {
        // maid: localStorage.getItem('maid'),
        nowmpid: '',
        content: '',
        uid: localStorage.getItem('uid')
      }
    },
    methods: {
      ...mapActions({
        chooseop: "qwe/chooseop",
        getprogress: "qwe/getprogress",
        showmsg: "qwe/showmsg",
        getmissionbyid: 'qwe/getmissionbyid'
      }),
      huifu(item) {
        this.nowmpid === item.mpid ? this.nowmpid = '' : this.nowmpid = item.mpid;
      },
      hfhf(item) {
        this.$http('/savehf', {
          mid: localStorage.getItem('mid'),
          mpid: this.nowmpid,
          content: this.content,
          uid: localStorage.getItem('uid'),
          touid: item.uid
        }, rs => {
          this.nowmpid = '';
          this.content = '';
          this.getprogress();
          this.socket.emit('msg2');
        })
      },
      fm() {
        this.showmsg({
          text: "确认完成该任务？",
          q: "finishmission",
          para: {
            mid: this.qwe.mid,
            state: "已完成"
          }
        });
      },
      formatepl(i) {
        if (i === 1) {
          return "最优先";
        } else if (i === 2) {
          return "高";
        } else if (i === 3) {
          return "中";
        } else if (i === 4) {
          return "低";
        } else if (i === 5) {
          return "长期";
        }
      }
    },
    computed: {
      ...mapState("qwe", {
        qwe: state => state.missioninfo,
        documentwidth: state => state.documentwidth,
        progressinfo: state => state.progressinfo
      })
    },
    mounted() {
      //console.log(this.qwe);
      this.getprogress();
      this.getmissionbyid();
    }
  };

</script>

<style scoped>
  .iconc {
    width: 35px;
    height: 35px;
    border-radius: 35px;
    float: left;
  }

  .hflist2 {
    font-size: 14px;
    font-weight: 300;
  }

  .hflist1 img {
    width: 30px;
    height: 30px;
    border-radius: 30px;
  }

  .hflist1 {
    font-weight: 300;
    font-size: 13px;
    color: #555;
  }

  .hflist {
    background: #ddd;
    margin: 0px 10px;
    padding: 5px;
    border-bottom: 1px solid #ccc;
  }

  .hflistc {
    overflow: hidden;
    width: 100%;
  }

  .hfhfbtn {
    margin-right: 10px;
  }

  .hfhft textarea {
    width: 100%;
    resize: none;
    font-weight: 300;
    font-size: 15px;
    line-height: 20px;
    font-family: inherit;
  }

  .hfhft {
    margin: 3px 10px;
  }

  .hfhfc {
    overflow: hidden;
    width: 100%;
  }

  .huifubtn {
    width: 70px;
    border-radius: 30px;
  }

  div {
    word-break: break-all;
  }

  button {
    width: 100px;
    font-size: 14px;
    height: 30px;
    margin: 10px 0px;
    float: right;
  }

  .subtitle {
    font-weight: 300;
    font-size: 14px;
    padding: 3px 0px;
    border-bottom: 1px solid #ccc;
    text-indent: 10px;
  }

  .timelistcontent {
    padding: 10px;
  }

  .timelisttitle img {
    width: 40px;
    height: 40px;
    border-radius: 40px;
  }

  .timelisttitle {
    padding: 5px;
    font-weight: 300;
    border-bottom: 1px solid #ccc;
    border-left: 8px solid #ae7ab0;
  }

  .timelist {
    margin: 10px 0px;
    background: #eee;
    padding-bottom: 10px;
  }

  .conbox {
    width: 50%;
    float: left;
    border-right: 1px solid #ccc;
    padding: 10px;
  }

  .content {
    border: 1px solid #ccc;
    padding: 10px;
    border-radius: 5px;
    background: #f5f5f5;
    font-size: 16px;
    font-weight: 300;
  }

  .time {
    font-size: 15px;
    text-align: center;
    line-height: 40px;
    font-weight: 300;
  }

  .title {
    font-size: 20px;
    text-align: center;
    line-height: 40px;
  }

  .backbtn {
    position: absolute;
    left: 10px;
    cursor: pointer;
  }

  .toolbar {
    background: #000;
    color: #fff;
    height: 40px;
    line-height: 40px;
    font-weight: 300;
    border-radius: 5px;
    margin-bottom: 15px;
    position: relative;
  }

</style>



