<template>
  <div>
    <input type="text" v-model='t1'>
    <input type="text" v-model='t2'>
    <button @click="test">test</button>

    <div v-for="item in p">{{item}}</div>
  </div>
</template>

<script>
  import {
    mapState,
    mapActions
  } from "vuex";
  import io from 'socket.io-client'

  export default {
    name: 'test',
    methods: {
      test() {

        this.socket.emit('test', {
          t1: this.t1,
          t2: this.t2
        });
      },
      ...mapActions({
        showmsg: "qwe/showmsg"
      })
    },
    data() {
      return {
        t1: 0,
        t2: 0,
        p: [],
      }
    },
    mounted() {
      this.socket.on('_login', obj => {
        this.p.push(obj + '上线了')
      })
    }
  }

</script>

<style scoped></style>



