<template>
  <div id="calendar">
    <!-- 年份 月份 -->
    <div class="month">
      <ul>
        <!--点击会触发pickpre函数，重新刷新当前日期 @click(vue v-on:click缩写) -->
        <li class="arrow" @click="pickPre(currentYear,currentMonth)">❮</li>
        <li class="year-month" @click="pickYear(currentYear,currentMonth)">
          <span class="choose-year">{{ currentYear }}</span>
          <span class="choose-month">{{ currentMonth }}月</span>
        </li>
        <li class="arrow" @click="pickNext(currentYear,currentMonth)">❯</li>
      </ul>
    </div>
    <!-- 星期 -->
    <ul class="weekdays">
      <li>一</li>
      <li>二</li>
      <li>三</li>
      <li>四</li>
      <li>五</li>
      <li style="color:red">六</li>
      <li style="color:red">日</li>
    </ul>
    <!-- 日期 -->
    <ul class="days">
      <!-- 核心 v-for循环 每一次循环用<li>标签创建一天 -->
      <li v-for="(dayobject,i) in days" :key="i" @click="_selectrili(formatcd(dayobject))">
        <!--本月-->
        <!--如果不是本月  改变类名加灰色-->

        <span v-if="dayobject.day.getMonth()+1 != currentMonth" class="other-month">{{ dayobject.day.getDate() }}</span>

        <!--如果是本月  还需要判断是不是这一天-->
        <span v-else>
          <!--今天  同年同月同日-->
          <span v-if="dayobject.day.getFullYear() == new Date().getFullYear() && dayobject.day.getMonth() == new Date().getMonth() && dayobject.day.getDate() == new Date().getDate()"
            class="active">{{ dayobject.day.getDate() }}</span>
          <span v-else>{{ dayobject.day.getDate() }}</span>
        </span>

        <div style="height:78px;overflow:auto;" v-if="documentwidth>=500" :title="formatnum(formatcd(dayobject))">
          <div class="qweqwe" :class="item.az==='getlist'?['blue']:['green']" v-for="(item,i) in rilidata[formatcd(dayobject)]" :key="i">
            <i style="margin-right:5px;" :class="item.az==='getlist'?['icon-emailfilling']:['icon-favoritesfilling']" class="iconfont"></i>{{item.title}}
            <!-- <i style="margin-right:5px;" :class="item.az==='getlist'?['green','icon-emailfilling']:['blue','icon-favoritesfilling']" class="iconfont"></i>{{item.title}} -->
          </div>
        </div>

        <div v-else :class="rilidata[formatcd(dayobject)]?'boxbggreen':'boxbgblue'">
          {{formatnum2(formatcd(dayobject))}} </div>
      </li>
    </ul>
  </div>
</template>

<script>
  // #eed7d7 红
  // #edd7ee 紫
  // #d7e2ee 蓝
  // #d9eed7 绿
  // #eee8d7 橙

  //dayobject.day.getFullYear()+'-'+((dayobject.day.getMonth()+1)<10? '0'+(dayobject.day.getMonth()+1):(dayobject.day.getMonth()+1))+ '-'+ (dayobject.day.getDate()<10? '0'+dayobject.day.getDate():dayobject.day.getDate())

  import {
    mapState,
    mapActions
  } from "vuex";

  export default {
    name: "rili",
    computed: {
      ...mapState("qwe", {
        rilidata: state => state.rili.data,
        documentwidth: state => state.documentwidth
      })
    },
    data() {
      return {
        currentDay: 1,
        currentMonth: 1,
        currentYear: 1970,
        currentWeek: 1,
        days: [],
        missionlist: [1, 2, 3, 4, 5, 6, 7, 8, 9]
      };
    },
    created: function () {
      //在vue初始化时调用
      this.initData(null);
    },
    mounted() {
      this.setmonth2local();
      this.getrilidata();
    },
    methods: {
      nowmonth() {
        return this.currentYear +
          "-" +
          (this.currentMonth < 10 ? "0" + this.currentMonth : this.currentMonth);
      },
      setmonth2local() {
        localStorage.setItem('nowmonth', this.nowmonth());
      },
      _selectrili(date) {
        localStorage.setItem('nowday', date);
        this.$router.push('/Tablec')
      },
      formatcd(dayobject) {
        return dayobject.day.getFullYear() +
          '-' + ((dayobject.day.getMonth() + 1) < 10 ? '0' + (dayobject.day.getMonth() + 1) : (dayobject.day.getMonth() +
            1)) +
          '-' + (dayobject.day.getDate() < 10 ? '0' + dayobject.day.getDate() : dayobject.day.getDate())
      },
      formatnum2(date) {
        var info = this.rilidata[date];

        var getnum = 0;
        var postnum = 0;
        if (info && info.length) {
          for (var i = 0; i < info.length; i++) {
            var _info = info[i];
            if (_info.az === "getlist") {
              getnum++;
            } else if (_info.az === "postlist") {
              postnum++;
            }
          }
        }
        return getnum + "\r\n/" + postnum;
      },
      formatnum(date) {
        var info = this.rilidata[date];

        var getnum = 0;
        var postnum = 0;
        if (info && info.length) {
          for (var i = 0; i < info.length; i++) {
            var _info = info[i];
            if (_info.az === "getlist") {
              getnum++;
            } else if (_info.az === "postlist") {
              postnum++;
            }
          }
        }
        return date + "\r\n获得" + getnum + "个任务\r\n发出" + postnum + "个任务";
      },
      ...mapActions({
        getrilidata: "qwe/getrilidata",
        selectrili: "qwe/selectrili"
      }),
      initData: function (cur) {
        var leftcount = 0; //存放剩余数量
        var date;

        if (cur) {
          date = new Date(cur);
        } else {
          var now = new Date();
          var d = new Date(this.formatDate(now.getFullYear(), now.getMonth(), 1));
          d.setDate(35);
          date = new Date(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1));
        }
        this.currentDay = date.getDate();
        this.currentYear = date.getFullYear();
        this.currentMonth = date.getMonth() + 1;

        this.currentWeek = date.getDay(); // 1...6,0
        if (this.currentWeek == 0) {
          this.currentWeek = 7;
        }
        var str = this.formatDate(
          this.currentYear,
          this.currentMonth,
          this.currentDay
        );
        this.days.length = 0;
        // 今天是周日，放在第一行第7个位置，前面6个
        //初始化本周
        for (var i = this.currentWeek - 1; i >= 0; i--) {
          var d = new Date(str);
          d.setDate(d.getDate() - i);
          var dayobject = {}; //用一个对象包装Date对象  以便为以后预定功能添加属性
          dayobject.day = d;
          this.days.push(dayobject); //将日期放入data 中的days数组 供页面渲染使用
        }
        //其他周
        for (var i = 1; i <= 42 - this.currentWeek; i++) {
          var d = new Date(str);
          d.setDate(d.getDate() + i);
          var dayobject = {};
          dayobject.day = d;
          this.days.push(dayobject);
        }
      },
      pickPre: function (year, month) {
        // setDate(0); 上月最后一天
        // setDate(-1); 上月倒数第二天
        // setDate(dx) 参数dx为 上月最后一天的前后dx天
        var d = new Date(this.formatDate(year, month, 1));
        d.setDate(0);
        this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1));

        this.setmonth2local();
        this.getrilidata();
      },
      pickNext: function (year, month) {
        var d = new Date(this.formatDate(year, month, 1));
        d.setDate(35);
        this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1));

        this.setmonth2local();
        this.getrilidata();
      },
      pickYear: function (year, month) {
        alert(year + "," + month);
      },

      // 返回 类似 2016-01-02 格式的字符串
      formatDate: function (year, month, day) {
        var y = year;
        var m = month;
        if (m < 10) m = "0" + m;
        var d = day;
        if (d < 10) d = "0" + d;
        return y + "-" + m + "-" + d;
      }
    }
  };

</script>

<style scoped>
  .boxbgblue {
    height: 78px;
    overflow: hidden;
    width: 100%;
    background: #d7e2ee;
  }

  .boxbggreen {
    height: 78px;
    overflow: hidden;
    width: 100%;
    background: #d9eed7;
  }

  .blue {
    background: #d7e2ee;
  }

  .green {
    background: #6cbc7c;
    color: #fff;
  }

  .qweqwe {
    border-bottom: 1px solid #eee;
    margin: 3px;
    padding: 3px;
    text-align: left;
    font-weight: 300;
    font-size: 13px;
  }

  #calendar {
    width: 100%;
    margin: 0 auto;
  }

  .month {
    width: 100%;
    background: #555;
  }

  .month ul {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-between;
    font-weight: 300;
  }

  .year-month {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
  }

  .year-month:hover {
    background: rgba(150, 2, 12, 0.1);
  }

  .choose-year {
    padding-left: 20px;
    padding-right: 20px;
  }

  .choose-month {
    text-align: center;
    font-size: 1.5rem;
  }

  .arrow {
    padding: 30px;
  }

  .arrow:hover {
    background: rgba(100, 2, 12, 0.1);
  }

  .month ul li {
    color: #fff;
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 3px;
    list-style-type: none;
    cursor: pointer;
  }

  .weekdays {
    margin: 0;
    padding: 10px 0;
    background-color: #000;
    display: flex;
    flex-wrap: wrap;
    color: #ffffff;
    justify-content: space-around;
    font-weight: 300;
  }

  .weekdays li {
    display: inline-block;
    width: 13.6%;
    text-align: center;
  }

  .days {
    padding: 0;
    background: #ffffff;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    border: 1px solid #ccc;
  }

  .days li {
    list-style-type: none;
    display: inline-block;
    width: 14.2%;
    text-align: center;
    padding-bottom: 15px;
    padding-top: 15px;
    font-size: 1rem;
    color: #000;
    height: 120px;
    /* border: 1px solid #eee; */
    border-bottom: 1px solid #ddd;
    cursor: pointer;
    font-weight: 300;
  }

  .days li .active {
    padding: 0px 10px;
    border-radius: 10px;
    background: #abc;
    color: #fff;
  }

  .days li .other-month {
    padding: 5px;
    color: gainsboro;
  }

  .days li:hover {
    background: #e1e1e1;
  }

</style>



