<template>
  <div class="back">
    <div class="content" :style="{margin:'100px '+(documentwidth<=1000?'10px':'auto')+''}">
      <div class="closebtn" @click="showeditor(false)">×</div>
      <div class="opcontent">
        <div class="wrbox">
          <!-- :style="{width:''+(documentwidth<=1000?100:50)+'%'}" -->
          <input type="text" placeholder="任务标题" v-model="title">
          <div id="editorElem" style="text-align:left"></div>
        </div>
        <div class="wrbox" style="padding:10px;">
          <!-- :style="{width:''+(documentwidth<=1000?100:50)+'%'}" -->
          <div class="wrboxd">
            <div class="lititle">任务成员</div>
            <div v-for="(item,i) in userlist" :key="i" class="psty" @click="chooseteam(item.uid)" :class="uidlist.indexOf(item.uid) >= 0?['pstyactive']:[]">
              {{item.uname}}
            </div>
          </div>
          <div class="wrboxd">
            <div class="lititle">期望时间（可选）</div>
            <div style="margin-top:15px;cursor:pointer;" @click="opendatepicker(1)">
              <i class="iconfont icon-clock" style="margin-right:10px;"></i>{{editor.time1}}
            </div>
          </div>
          <div class="wrboxd">
            <div class="lititle">最后期限</div>
            <div style="margin-top:15px;cursor:pointer;" @click="opendatepicker(2)">
              <i class="iconfont icon-clock" style="margin-right:10px;"></i>{{editor.time2}}
            </div>
          </div>
          <div class="wrboxd">
            <div class="lititle">优先级</div>
            <div v-for="(item,i) in pl" :key="i" class="psty" @click="choosepl(item)" :class="nowpl===item?['plactive']:[]">
              {{item}}
            </div>
          </div>
        </div>
      </div>
      <div style="margin-top:10px;    overflow: hidden;">
        <!-- <div class="errorinfo">qwe</div> -->
        <button class="commitbtn" @click="getContent">提交</button>
      </div>
    </div>
  </div>
</template>

<script>
  import E from "wangeditor";
  import {
    mapState,
    mapActions
  } from "vuex";
  import Datepicker from "./Datepicker";
  var editor;

  export default {
    name: "editor",
    components: {
      Datepicker
    },
    computed: {
      ...mapState("qwe", {
        time: state => state.rili.nowselect,
        documentwidth: state => state.documentwidth,
        userlist: state => state.userlist,
        editor: state => state.editor,
      })
    },
    methods: {
      ...mapActions({
        missioncommit: "qwe/missioncommit",
        getuserlist: "qwe/getuserlist",
        showmsg: "qwe/showmsg",
        opendatepicker: 'qwe/opendatepicker',
        showeditor: 'qwe/showeditor'
      }),
      getContent: function () {
        if (this.title === "") {
          this.showmsg({
            text: "请填写任务标题。",
            q: 0
          });
        } else if (this.editorContent === "") {
          this.showmsg({
            text: "请填写任务内容。",
            q: 0
          });
        } else if (this.uidlist.length === 0) {
          this.showmsg({
            text: "请选择成员。",
            q: 0
          });
        } else if (this.editor.time2 === "") {
          this.showmsg({
            text: "请选择最后期限。",
            q: 0
          });
        } else if (this.nowpl === "") {
          this.showmsg({
            text: "请选择优先级。",
            q: 0
          });
        } else {
          this.missioncommit({
            authorid: localStorage.getItem('uid'),
            title: this.title,
            name: this.editorContent,
            uidlist: this.uidlist,
            time1: this.editor.time1,
            time2: this.editor.time2,
            nowpl: this.nowpl
          });
        }
      },
      chooseteam(id) {
        if (this.uidlist.indexOf(id) >= 0) {
          this.uidlist.splice(this.uidlist.indexOf(id), 1);
        } else {
          this.uidlist.push(id);
        }
      },
      choosepl(item) {
        this.nowpl = item;
      }
    },
    data() {
      return {
        editorContent: "",
        title: "",
        uidlist: [],
        pl: ["最优先", "高", "中", "低", "长期"],
        nowpl: ""
      };
    },
    mounted() {
      this.getuserlist();
      editor = new E("#editorElem");
      editor.customConfig.uploadImgServer = "http://www.mobius-vision.com:4000/qwe?uid=" + localStorage.getItem('uid');
      editor.customConfig.menus = [
        "head", // 标题
        "strikeThrough", // 删除线
        "quote", // 引用
        "emoticon", // 表情
        "table", // 表格
        "image",
        "code" // 插入代码
      ];
      editor.customConfig.onchange = html => {
        this.editorContent = html;
      };
      console.log(editor)
      editor.create();
    }
  };

</script>

<style scoped>
  .opcontent {
    max-height: 50vh;
    overflow: auto;
    border-bottom: 2px solid #0090cc;
    -webkit-overflow-scrolling: touch;
  }

  .closebtn {
    background: #0090cc;
    width: 25px;
    height: 25px;
    position: absolute;
    right: -8px;
    top: -8px;
    text-align: CENTER;
    line-height: 20px;
    font-size: 18px;
    border: 2px solid #fff;
    color: #fff;
    border-radius: 25px;
    cursor: pointer;
    transition: background 0.2s ease;
    user-select: none;
  }

  .closebtn:hover {
    background: #1472a4;
  }

  .content {
    overflow: visible;
    background: #fff;
    padding: 10px;
    box-shadow: 0px 5px 5px #555;
    border-radius: 5px;
    max-width: 1000px;

    position: relative;
  }

  .back {
    width: 100vw;
    height: 100vh;
    position: fixed;
    left: 0px;
    top: 0px;
    background: rgba(0, 0, 0, 0.4);
    z-index: 999999;
  }

  .datepickercon {
    width: 100vw;
    height: 100vh;
    left: 0px;
    top: 0px;
    background: rgba(0, 0, 0, 0.4);
    position: fixed;
    z-index: 999999;
  }

  .wrboxd {
    overflow: hidden;
    margin-bottom: 20px;
    position: relative;
  }

  .plactive {
    background: #cf7f7f !important;
    color: #fff;
  }

  .pstyactive {
    background: #af68c2 !important;
    color: #fff;
  }

  .psty:hover {
    background: #ddd;
  }

  .psty {
    margin: 5px;
    padding: 5px 10px;
    background: #eee;
    float: left;
    border-radius: 5px;
    cursor: pointer;
  }

  .lititle {
    border-bottom: 1px solid #ccc;
  }

  .wrbox {
    float: left;
    overflow: hidden;
    width: 100%;
  }

  .errorinfo {
    float: left;
    color: red;
  }

  input {
    height: 40px;
    width: 100%;
    border: 1px solid #ccc;
    border-radius: 0px;
    outline: none;
    font-size: 18px;
    font-weight: 300;
    -webkit-appearance: none;
    margin-bottom: -1px;
  }

  .commitbtn {
    width: 100px;
    float: right;
  }

</style>



