<template>
  <transition name="fade">
    <div class="back" v-if="msg.flg">
      <div class="box">
        <div class="closebtn" @click="msgbtn(0)">×</div>
        <div class="content">{{msg.content}}</div>
        <div style="float:right">
          <!-- <button @click="msgbtn(0)">Cancel</button> -->
          <button @click="msgbtn(msg)">好的</button>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  import {
    mapState,
    mapActions
  } from "vuex";
  export default {
    name: "msg",
    computed: {
      ...mapState("qwe", {
        msg: state => state.msg
      })
    },
    methods: {
      ...mapActions({
        msgbtn: "qwe/msgbtn"
      })
    }
  };

</script>

<style scoped>
  .fade-enter-active,
  .fade-leave-active {
    transition: opacity .2s;
  }

  .fade-enter,
  .fade-leave-active {
    opacity: 0;
  }

  .closebtn {
    background: #0090cc;
    width: 25px;
    height: 25px;
    position: absolute;
    right: -8px;
    top: -8px;
    text-align: CENTER;
    line-height: 20px;
    font-size: 18px;
    border: 2px solid #fff;
    color: #fff;
    border-radius: 25px;
    cursor: pointer;
    transition: background 0.2s ease;
    user-select: none;
  }

  .closebtn:hover {
    background: #1472a4;
  }

  .content {
    padding-bottom: 10px;
    overflow: hidden;
    word-break: break-all;
    font-size: 15px;
    font-weight: 300;
  }

  button {
    width: 80px;
    font-size: 15px;
    height: 30px;
    line-height: 30px;
  }

  .box {
    width: 280px;
    background: #fff;
    transform: translate(-50%, -50%);
    left: 50%;
    top: 50%;
    position: absolute;
    padding: 15px;
    border-radius: 5px;
    box-shadow: 0px 5px 5px #555;
  }

  .back {
    width: 100%;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.4);
    z-index: 99999999;
  }

</style>



